#root {
  width: 100%;
}

body {
  margin: 0;
  background-color: #2b2322;
  font-family: Calibri, sans-serif;
}
.appWrapper {
  display: grid;
  grid-template-columns: 3fr 10fr;
  grid-template-rows: 1fr 10fr;
  grid-template-areas:
    'header header'
    'navbar content';
  column-gap: 35px;
  width: 1100px;
  height: 800px;
  margin: 0 auto;
}

.contentWrapper {
  grid-area: content;
  height: 100%;
  background-color: #392f2d;
  /*border: 1px solid #73605b;*/
  border-radius: 0.25rem;
  color: white;
  /*max-height: 800px;*/
  min-height: 700px;
  padding: 20px;
}
