.navbarItemWrapper {
  border-bottom: 7px solid #2b2322;
  text-align: center;
  padding-top: 15px;
  color: white;
  text-decoration: none;
  font-size: 19px;
}

.navbarItemWrapper:last-child {
  border-bottom: none;
}

.navbarItemWrapper:hover {
  background-color: #473b38;
}

.navbarItemWrapper:first-child:hover,
.activeNavbarLink:first-child {
  border-radius: 0.25rem 0.25rem 0 0;
}

.navbarItemWrapper:last-child:hover,
.activeNavbarLink:last-child {
  border-radius: 0 0 0.25rem 0.25rem;
}

.activeNavbarLink {
  background-color: #73605b;
}
